  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  display: flex;
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
}

.title {
  text-transform: uppercase;
  color: #fff;
  margin: 30px 0 0;
}

.search_input {
  width: 285px;
  outline: rgba(0, 0, 0, 0);
}

.btn {
  width: 37px;
  height: 35px;
  cursor: pointer;
  background: #1b2735;
  border: none;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 16.3%;
  left: 48%;
  transform: translate(365%, -50%);
}

.days_query {
  justify-content: center;
  align-items: center;
