  width: 40px;
  height: 37px;
  color: #fff;
  cursor: pointer;
  background: #1b2735;
  border-radius: 50%;
  outline: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  font-size: 14px;
  display: flex;
}

